html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 20px;
  background-color: #cecece;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}
@media only screen and (min-width: 1000px) {
  body {
    padding: 1.5rem;
  }
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}

#main_container {
  background-color: #ffffff;
}

#items_container {
  background-color: #ffffff;
  padding: 5px;
}
#items_container .item {
  border: 1px solid #cecece;
  margin: 5px;
  padding: 5px;
  max-width: 150px;
  width: 100%;
}
#items_container .item_img {
  display: block;
  width: 100%;
  height: initial;
  object-fit: contain;
}
#items_container .item_title, #items_container .item_price {
  display: block;
  text-align: center;
}
#items_container .item_price {
  font-weight: 700;
}

.logo_container {
  padding: 5px;
}
.logo_container img {
  display: block;
  max-width: 100%;
  height: initial;
  object-fit: contain;
}